}
ul.ul li {
  display: block;
  font-size: 16px;
  line-height: 1.4;
  margin-bottom: 8px;
  margin-left: 0;
  position: relative;
  vertical-align: top;
}
ul.ul li:before {
  background-color: #1476fb;
  border-radius: 50%;
  height: 4px;
  width: 4px;
  content: '';
  display: block;
  left: -12px;
  line-height: 1;
  position: absolute;
  top: 0.5em;
}
.wrapper {
  margin: 0 auto;
  padding-left: 1.2rem;
  padding-right: 1.2rem;
  width: 100%;
