.empty {
  height: 18rem;
}
.header {
  padding: 3.2rem 0;
  position: fixed;
  top: 0;
  transition: background-color 0.3s ease, padding 0.3s ease;
  width: 100%;
  z-index: 300;
}
.header__container {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.header__left {
  align-items: center;
  display: flex;
}
.header .lang__item {
  opacity: 0.5;
  transition: all 0.3s ease;
}
.header .lang__item:not(:last-child) {
  margin-right: 1em;
}
.header .lang__item:hover {
  color: #1476fb;
  opacity: 1;
}
.header .lang__item--active {
  color: #1476fb;
  opacity: 1;
}
.header--white .logo--black {
  display: none;
}
.header--white .nav {
  color: #fff;
}
.header--white .burger:before,
.header--white .burger:after {
  background-color: #fff;
}
.header--white .lang__item {
  color: #fff;
}
.header--scroll {
  background-color: #fff;
  padding: 2.4rem 0;
}
.header--scroll .logo--white {
  display: none;
}
.header--scroll .logo--black {
  display: block;
}
.header--scroll .nav {
  color: #1e1f21;
}
.header--scroll .lang__item {
  color: #1e1f21;
}
.header--scroll .burger:before,
.header--scroll .burger:after {
  background-color: #1e1f21;
}
.nav:hover .nav__link {
  opacity: 0.5;
}
.nav__link {
  font-size: 1.6rem;
  margin-right: 2em;
  transition: all 0.3s ease;
}
.nav__link:hover {
  color: #1476fb;
  opacity: 1 !important;
}
.burger {
  display: none;
  position: relative;
  transform: translateY(-4px);
  width: 2.4rem;
}
.burger:before,
.burger:after {
  background-color: #1e1f21;
  content: '';
  height: 2px;
  position: absolute;
  top: 0;
  transition: all 0.3s ease;
  width: 2.4rem;
}
.burger:after {
  top: 8px;
}
.burger--active:before {
  transform: rotate(45deg) translateY(6px);
}
.burger--active:after {
  transform: rotate(-45deg) translateY(-6px);
}
.mobile-menu {
  background-color: #fff;
  box-shadow: 0px 40px 16px -2px rgba(0,0,0,0.15);
  display: none;
  left: 0;
  padding: 2.4rem 1.2rem;
  position: fixed;
  top: 4.8rem;
  width: 100%;
  z-index: 500;
}
.mobile-menu__link {
  display: block;
  font-size: 2.4rem;
}
.mobile-menu__link:not(:last-child) {
  margin-bottom: 1em;
}
@media (max-width: 1200px) {
  .empty {
    height: 10rem;
  }
  .header__free {
    color: #1476fb;
  }
  .nav__link {
    font-size: 1.4rem;
  }
}
@media (max-width: 1000px) {
