  }
  .header__free {
    display: none;
  }
  .nav {
    display: none;
  }
  .burger {
    display: block;
  }
}
.support-tables .support-table {
  border: 1px solid #dbe1e9;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}
.support-tables .support-table__top {
  padding: 32px;
  padding-bottom: 0;
}
.support-tables .support-table .ul {
  margin-bottom: 24px;
}
.support-tables .support-table__price {
  align-items: flex-end;
  display: flex;
  margin-bottom: 0.5em;
  padding-left: 32px;
}
.support-tables .support-table__price-number {
  font-size: 24px;
  font-weight: 600;
  margin-right: 0.3em;
}
.support-tables .support-table__price-comment {
  display: block;
  transform: translateY(-2px);
}
.support-tables .support-table__price + .support-table__price {
  margin-bottom: 32px;
}
.support-tables .support-table__footer {
  flex-grow: 0;
  width: 100%;
}
.support-tables .support-table__button {
  background-color: #f4f4f4;
  padding: 24px 32px;
}
